@import '~antd/dist/antd.less';

@primary-color: #4db6f7; // 全局主色
@layout-header-background: transparent;
@menu-bg: transparent;
@link-color: #4db6f7; // 链接色
@success-color: #52c41a; // 成功色
@warning-color: #faad14; // 警告色
@error-color: #f5222d; // 错误色
@font-size-base: 14px; // 主字号
@heading-color: #c8eaff; // 标题色
@text-color: #4db6f7; // 主文本色
@text-color-secondary: #1b57b3; // 次文本色
@disabled-color: rgba(126, 126, 126, 0.25); // 失效色
@border-radius-base: 2px; // 组件/浮层圆角
@border-color-base: #d9d9d9; // 边框色
@box-shadow-base: 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 6px 16px 0 rgba(0, 0, 0, 0.08),
  0 9px 28px 8px rgba(0, 0, 0, 0.05); // 浮层阴影

.logo {
  float: left;
  width: 180px;
  height: 50px;
  margin: 8px 12px 0px 0;
  background-image: url("https://firebasestorage.googleapis.com/v0/b/d4m-platform.appspot.com/o/Daylife_logo.png?alt=media&token=a9a62fb6-14a9-4623-9ddc-b97a0567011c");
  background-repeat: no-repeat;
  background-size: contain;
}
.ant-row-rtl .logo {
  float: right;
  margin: 16px 0 16px 24px;
}
  [data-theme="dark"] .site-layout-content {
    background: #141414;
  }

.ant-carousel {
  margin-bottom: 1em;
}
.ant-carousel .slick-slide {
  padding-bottom: 1em;
}
.ant-carousel .slick-slide img {
  width: auto;
}

form input {
  margin: 0.5em;
  border: 1px solid #000;
}
form button {
  margin: 0.5em;
}

.img-product {
  width: 150px;
}

.wrapInfo {
  display: block;
  word-break: break-word;
  word-wrap:break-word;
  white-space: pre-wrap;
}
